<template>
  <div class="wrapper">
    <img class="wrapper__img" src="http://www.dell-lee.com/imgs/vue3/user.png" alt="">
    <div class="wrapper__input">
      <input v-model="username" placeholder="用户名" type="text" class="wrapper__input__content">
    </div>
    <div class="wrapper__input">
      <input v-model="password" placeholder="密码" type="password" class="wrapper__input__content" autocomplete="new-password">
    </div>
    <div class="wrapper__login" @click="handleLogin">登录</div>
    <div class="wrapper__links">
      <span @click="handleRegister">立即注册</span>
      <span>|</span>
      <span>忘记密码</span>
    </div>
  </div>
  <Toast v-if="show" :message="toastMessage" />
</template>

<script>
import { useRouter } from 'vue-router'
import { reactive, toRefs } from 'vue'
import { post } from '../../utils/request'
import Toast, { useToastEffect } from '../../components/Toast.vue'

// 登录逻辑
const useLoginEffect = showToast => {
  const router = useRouter()
  const data = reactive({
    username: '',
    password: ''
  })
  // 登录
  const handleLogin = async () => {
    try {
      const result = await post('/api/user/login', {
        username: data.username,
        password: data.password
      })
      if (result.errno === 0) {
        showToast('登录成功')
        localStorage.isLogin = true
        router.push({ name: 'Home' })
      } else {
        showToast('登录失败')
      }
    } catch (e) {
      showToast('请求失败')
    }
  }
  const { username, password } = toRefs(data)
  return { username, password, handleLogin }
}

export default {
  name: 'Login',
  components: {
    Toast
  },
  setup () {
    const router = useRouter()
    const { show, toastMessage, showToast } = useToastEffect()
    const { username, password, handleLogin } = useLoginEffect(showToast)

    // 跳转注册
    const handleRegister = () => {
      router.push({ name: 'Register' })
    }
    return {
      handleLogin,
      handleRegister,
      username,
      password,
      show,
      toastMessage
    }
  }
}
</script>

